<template>
    <br><br><br><br><br>
    <!-- Game Menu Container -->
    <div class="mnguMenContainer">
        <div class="mnguMen">
            <h1>TESTID</h1>
            <!-- Game  Mode Selector -->
            <div class="radio-container">
                <div>
                    <input type="radio" id="pealinnadeMng" value="pealinnadeMng" v-model="selectedGame" class="hidden-radio">
                    <label for="pealinnadeMng" class="radio-label">
                        <span class="custom-radio"></span>
                        Pealinnade mng
                    </label>
                </div>
                <div>
                    <input type="radio" id="lippudeMng" value="lippudeMng" v-model="selectedGame" class="hidden-radio">
                    <label for="lippudeMng" class="radio-label">
                        <span class="custom-radio"></span>
                        Lippude mng
                    </label>
                </div>
            </div>
            <div class="radio-container">
                <div>
                    <input type="radio" id="lippudeMng2" value="lippudeMng2" v-model="selectedGame" class="hidden-radio">
                    <label for="lippudeMng2" class="radio-label">
                        <span class="custom-radio"></span>
                        Lippude mng 2
                    </label>
                </div>
            </div>
            <!-- Question Count Selector -->
            <div>
                <label id="labeliteTekst" for="questionCount">Ksimuste arv:</label>
                <select id="questionCount" v-model="questionCount">
                    <option v-for="n in 186" :key="n+9" :value="n+9">{{ n+9 }}</option>
                </select>
            </div>

            <!-- Continent Selector -->
            <div>
                <label class="valiRiigidLabel" id="labeliteTekst" for="questionCount">Vali riigid:</label>
            </div>
            <div class="mnguMenNupud">
                <div class="checkbox-container" v-for="continent in continents" :key="continent">
                    <label :for="continent" class="checkbox-label">
                        <input type="checkbox" :id="continent" :value="continent" v-model="selectedContinents">
                        <span class="checkmark" :style="{ 'background-color': selectedContinents.includes(continent) ? '#55E0E5' : '#eee' }"></span>
                        {{ continent }}
                    </label>
                </div>
            </div>
            <!-- Play with clues or not-->
            <label id="labeliteTekst" for="vihjetegaViIlma">Mngin vihjetega:</label>
            <br>
            <div class="radio-containerCol">
                <div>
                    <input type="radio" id="vihjetegaViIlmaJah" value="Jah" v-model="vihjetegaMng" class="hidden-radio">
                    <label for="vihjetegaViIlmaJah" class="radio-label">
                        <span class="custom-radio"></span>
                        Jah
                    </label>
                </div>
                <div>
                    <input type="radio" id="vihjetegaViIlmaEi" value="Ei" v-model="vihjetegaMng" class="hidden-radio">
                    <label for="vihjetegaViIlmaEi" class="radio-label">
                        <span class="custom-radio"></span>
                        Ei
                    </label>
                </div>
            </div>

            <!-- Start Game Button -->
            <button @click="startGame">Alusta mnguga</button>
        </div>
    </div>
    <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
</template>

<script>
    export default {
        name: "getMnguValimine",
        data() {
            return {
                questionCount: 10,
                continents: ['Phja-Ameerika', 'Kesk-Ameerika', 'Luna-Ameerika', 'Euroopa', 'Aafrika', 'Aasia', 'Okeaania', 'Kik riigid'],
                selectedContinents: [],
                selectedGame: null,
                mngValitud: false,
                vihjetegaMng: null,
            };
        },
        methods: {
            startGame() {
                if(!this.validateSelection()) return;
                if(this.vihjetegaMng === 'Ei'){
                    this.vihjetegaMng = false;
                }else{
                    this.vihjetegaMng = true;
                }
                
                console.log(`\Alustan ${this.selectedGame} with ${this.questionCount} questions and continents: ${this.selectedContinents.join(', ')}, vihjetega: ${this.selectedVihjetega}!`);
                this.$store.commit('startGame', {
                    selectedGame: this.selectedGame,
                    questionCount: this.questionCount,
                    selectedContinents: this.selectedContinents,
                    mngValitud: this.mngValitud,
                    vihjetegaMng: this.vihjetegaMng
            });
            this.$emit('start-game');
            },
            // Kontrollib, kas mngu tp on valitud
            validateSelection() {
                if (!this.selectedGame) {
                    console.error('A game type must be chosen!');
                    alert('Vali vhemalt ks mngu tp!')
                    return false;
                }
                if (this.selectedGame !== 'pealinnadeMng' && this.selectedGame !== 'lippudeMng' && this.selectedGame !== 'lippudeMng2') {
                    console.error('Invalid game type!');
                    return false;
                }
                if(this.vihjetegaMng === ''){
                    console.error('Vihjetega vi ilma mngimine peab olema valitud!');
                    alert('Vali, kas soovid mngida vihjetega vi ilma!')
                    return false;
                }
                return true;
            }
        },
    }
</script>

<style scoped>

@media (max-width: 400px) {
    .mnguMen {
      padding: 0ch;
    }
}

@media (max-width: 700px) {
    .mnguMen {
      flex-direction: column;
      padding: 2ch;
      align-items: flex-start; /* Add this line */
    }
  
    .mnguMen div {
      flex-direction: column;
      margin-bottom: 15px;
    }
  
    .mnguMen > button {
      width: 100%;
      margin-top: 20px;
    }
  
    .radio-container, .radio-containerCol {
      flex-direction: column;
    }
  
    .checkbox-container {
      margin-bottom: 15px;
      align-self: flex-start;
    }
  
    #labeliteTekst {
      font-size: 18px;
    }
  }

    .mnguMenContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #0B1C24;
        padding: 20px;
        border-radius: 20px;
    }

    .mnguMen {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        background-color: #0B1C24;
        padding: 4ch;
        padding-top: 2ch;
        border-radius: 10px;
    }
    
    .mnguMen div {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;
    }

    .mnguMenNupud {
        flex-direction: column;
        align-items: flex-start; /* Add this line */
        padding-top: 1ch;
    }

    .mnguMen label {
        color: #55E0E5;
        padding-right: 2ch;
    }
    #labeliteTekst{
        font-size: 22px;
        align-self: flex-start;
        text-decoration: none;
        user-select: none;
    }

    .mnguMen > button {
        background-color: #55E0E5;
        margin-top: 30px;
        border-radius: 36px;
        border: 0;
        font-weight: 700;
        font-size: 1em;
        padding: 10px 16px;
        letter-spacing: 2px;
        cursor: pointer;
        transition: background-color 0.3s;
        width: 100%;
    }

    .mnguMen > button:hover {
        background-color: #33d9ff;
    }

    .valiRiigidLabel {
        padding-top: 1ch;
    }

    .radio-containerCol{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    
    /* Radio nupud */
    .radio-container {
        display: flex;
        justify-content: space-between;
    }

    .hidden-radio {
        display: none;
    }

    .radio-label {
        font-size: 22px;
        position: relative;
        padding-left: 35px;
        text-decoration: none;
        user-select: none;
        cursor: pointer;
        white-space: nowrap;
    }

    .custom-radio {
        position: absolute;
        top: 0;
        left: 0;
        height: 22px;
        width: 22px;
        background-color: #fff;
        border-radius: 50%;
        border: 2px solid #55E0E5;
        text-decoration: none;
        user-select: none;
        cursor: pointer;
    }

    .hidden-radio:checked ~ .radio-label .custom-radio {
        background-color: #55E0E5;
    }

    .hidden-radio:checked ~ .radio-label .custom-radio::after {
        content: "";
        display: block;
        border-radius: 50%;
        width: 12px;
        height: 12px;
        margin: 5px;
        background: #fff;
    }

    /* Custom Checkbox */
    .checkbox-container {
        display: block;
        position: relative;
        padding-left: 35px;
        margin-bottom: 12px;
        cursor: pointer;
        font-size: 22px;
        user-select: none;
    }

    .checkbox-container input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
    }
    .checkbox-label {
        display: flex;
        align-items: center;
        cursor: pointer;
    }
    /* checkmark kastid */
    .checkmark {
        position: relative;
        top: 0;
        left: 0;
        padding-right: 0px;
        margin-right: 1ch;
        height: 25px;
        width: 25px;
        background-color: white;
        border-radius: 4px;
    }

    .checkbox-container:hover input ~ .checkmark,
    .checkbox-container input:focus ~ .checkmark {
        background-color: white;
    }

    .checkbox-container input:checked ~ .checkmark {
        background-color: #55E0E5;
    }

    .checkmark:after {
        content: "";
        position: absolute;
        display: none;
    }

    .checkbox-container input:checked ~ .checkmark:after,
    .checkbox-container input:focus ~ .checkmark:after {
        display: block;
    }

   .checkbox-container .checkmark:after {
        left: 9px;
        top: 5px;
        width: 5px;
        height: 10px;
        border: solid white;
        border-width: 0 3px 3px 0;
        transform: rotate(45deg);
    }
    
    
</style>