<template>
    <div class="mainContent">
        <div class="tabeliMen">
            <div>
                <h1>PEALINNAD</h1>
            </div>
            <div></div>
            <div class="tabeliMenFiltrid">
                <div class="tabeliMenInner">
                    <label class="labelAsukoht">Filtreeri asukoha jrgi</label>
                    <select class="valiAsukoht" v-model="asukohtFilter">
                        <option value="">Kik asukohad ({{ andmed.length }})</option>
                        <option v-for="option in asukohtOptions" :value="option">{{ option }} ({{ asukohtCounts[option] }})</option>
                    </select>
                </div>
                <div class="tabeliMenInner">
                    <label class="labelNimi">Filtreeri riigi nime jrgi</label>
                    <input class="valiRiik" type="text" placeholder="Filtreeri riigi nime jrgi..." v-model="filter"/>
                </div>
            </div>
        </div>

        <v-table class="table">
            <thead>
                <tr>
                    <th>LIPP</th>
                    <th>RIIK</th>
                    <th>PEALINNA SEOS</th>
                    <th>ASUKOHT</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, index) in filteredRows" :key="index">
                    <td>
                        <img class="lipuJaSeosePilt" :src="require(`@/assets/lipud/${item.lipp}`)" height="100" alt="Riigi lipp">
                    </td>
                    <td v-html="highlightMatches(item.riik)"></td>
                    <td>
                        <span class="seoseJutt">
                            <p class="pealinn">{{ item.pealinn }}:</p>
                            <p v-html="item.seosJutt"></p>
                        </span>
                        <img class="lipuJaSeosePilt" :src="require(`@/assets/seosed/${item.seosPilt}`)" height="250" alt="Seos Pilt">
                    </td>
                    <td>{{ item.asukoht }}</td>
                </tr>
            </tbody>
        </v-table>
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    data() {
        return {
            andmed: [],
            filter: '',
            rows: [],
            asukohtFilter: '',
        }
    },

    computed: {
        ...mapState({
            ajutineData: state => state.kikRiigid
        }),

        asukohtOptions() {
            const options = new Set();
            this.andmed.forEach(item => options.add(item.asukoht));
            return Array.from(options);
        },

        asukohtCounts() {
            const counts = {};
            this.andmed.forEach(item => {
                if (!counts[item.asukoht]) {
                    counts[item.asukoht] = 0;
                }
                counts[item.asukoht]++;
            });
            return counts;
        },

        filteredRows(){
        return this.andmed.filter(item => {
            const riik = item.riik.toLowerCase();
            const searchTerm = this.filter.toLowerCase();
            const asukohtMatch = !this.asukohtFilter || item.asukoht === this.asukohtFilter;
            return riik.includes(searchTerm) && asukohtMatch;
        });
        }
    },

    methods: {
        highlightMatches(text) {
            const matchExists = text.toLowerCase().includes(this.filter.toLowerCase());
            if (!matchExists) return text;

            const re = new RegExp(this.filter, 'ig');
            return text.replace(re, matchedText => `<strong>${matchedText}</strong>`);
        }
    },
    created() {
        this.andmed = this.ajutineData.map(item => {  
            if(!item.nimi || !item.pealinn || !item.seosPealinn || !item.seosPealinnPilt || !item.lipp || !item.asukoht) {
                console.log('Andmed on puudulikud');
            }
            return {
                riik: item.nimi,
                pealinn: item.pealinn,
                seosJutt: item.seosPealinn,
                seosPilt: item.seosPealinnPilt,
                lipp: item.lipp,
                asukoht: item.asukoht
            }
        });
        this.rows = this.andmed.map(item => item.nimi);
    }
}
</script>

<style scoped>
.mainContent {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.table {
    width: 90%;
    border-collapse: collapse;
    border: 1px solid black;
}

.table th {
    background-color: #55E0E5;
    color: black;
    font-size: 25px;
}

.table td {
    background-color: white;
    font-size: 20px;
}

.table th, .table td {
    padding: 10px;
    border: 1px solid black;
}

.lipuJaSeosePilt{
    border: 1px solid black;
}

.tabeliMen {
    display: flex;
    justify-content: space-between;
    margin-top: 5ch;
    width: 90%;
}

.tabeliMenInner {
    float: right;
    display: flex;
    flex-direction: column;
}

.tabeliMenFiltrid{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
    width: 50%;
}

.tabeliMenFiltrid > div{
    margin: 10px;
}

.labelAsukoht {
    align-items: flex-start;
}
.labelAsukoht, .labelNimi {
    color:  #55E0E5;
    font-size: 20px;
    font-weight: bold;
}

.valiAsukoht {
    padding-left: 20px;
}

.valiRiik {
    align-self: flex-start;
}
.valiAsukoht, .valiRiik {
    width: 100%;
    padding: 5px;
    margin-top: 5px;
    margin-bottom: 10px;
}

.seoseJutt {
    display: flex;
    align-items: center;
}

.pealinn {
    font-weight: bold;
    margin-right: 10px;
    text-decoration: underline;
}

</style>