<template>
    <div class="mnguMenContainer">
        <mnguValimine v-if="!gameStartedPealinnad && !gameStartedLippudeMng && !gameStartedLippudeMng2" @start-game="startGame"></mnguValimine>
        <pealinnadeMng v-if="gameStartedPealinnad && !kasTestOnLpetatud" :imporditudAndmed="imporditudAndmed"  @test-lpetatud="handleTestLpetatud"></pealinnadeMng>
        <lippudeMng v-if="gameStartedLippudeMng && !kasTestOnLpetatud" :imporditudAndmed="imporditudAndmed"  @test-lpetatud="handleTestLpetatud"></lippudeMng>
        <lippudeMng2 v-if="gameStartedLippudeMng2 && !kasTestOnLpetatud" :imporditudAndmed="imporditudAndmed"  @test-lpetatud="handleTestLpetatud"></lippudeMng2>
        <!--
        <mnguLpp v-if="kasTestOnLpetatud" :imporditudAndmed="imporditudAndmed"></mnguLpp>
        -->
    </div>  
</template>

<script>
import mnguValimine from '../components/mnguValimine.vue';
import pealinnadeMng from './PealinnadeMng.vue';
import LippudeMng from './LippudeMng.vue';
import LippudeMng2 from './LippudeMng2.vue';
import mnguLpp from './mnguLpp.vue';

export default {
    name: "App",
    components: {
        mnguValimine,
        'pealinnadeMng': pealinnadeMng,
        'lippudeMng': LippudeMng,
        'lippudeMng2': LippudeMng2,
        mnguLpp
    },
    data() {
        return {
            gameStartedPealinnad: false,
            gameStartedLippudeMng: false,
            gameStartedLippudeMng2: false,
            kasTestOnLpetatud: false,
            imporditudAndmed: [],
            questionCount: 0,
            selectedContinents: [],
            selectedGame: '',
            mngValitud: this.$store.state.mngValitud
        };
    },
    computed: {},
    methods: {
        handleTestLpetatud(kasTestLppes) {
            this.kasTestOnLpetatud = kasTestLppes;
            if (this.kasTestOnLpetatud) {
                this.$router.push("/mnguLpp");
            }else{
                console.log('ERROR: Mngu lpetamine ebannestus');
            }
            
        },
        startGame() {
            this.selectedGame = this.$store.state.selectedGame;
            this.questionCount = this.$store.state.questionCount;
            this.selectedContinents = this.$store.state.selectedContinents;

            //let ajutisedAndmed = this.$store.getters.getAndmed;

            // Mngu valimine
            if(this.selectedGame === 'pealinnadeMng') {
                this.gameStartedPealinnad = true;
            }else if(this.selectedGame === 'lippudeMng') {
                this.gameStartedLippudeMng = true;
            }else if(this.selectedGame === 'lippudeMng2') {
                this.gameStartedLippudeMng2 = true;
            }else{
                console.log('Mngu valimine ebannestus');
            }

            //Valin andmed vastavalt valitud kontinentidele

            let ajutisedAndmed = [];

            if (this.selectedContinents.includes('Euroopa')) {
            console.log('Valitud: Euroopa');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getEuroopaAndmed);
            }
            if (this.selectedContinents.includes('Phja-Ameerika')) {
            console.log('Valitud: Phja-Ameerika');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getPhjaAmeerikaAndmed);
            }
            if (this.selectedContinents.includes('Kesk-Ameerika')) {
            console.log('Valitud: Kesk-Ameerika');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getKeskAmeerikaAndmed);
            }
            if (this.selectedContinents.includes('Luna-Ameerika')) {
            console.log('Valitud: Luna-Ameerika');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getLunaAmeerikaAndmed);
            }
            if (this.selectedContinents.includes('Aafrika')) {
            console.log('Valitud: Aafrika');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getAafrikaAndmed);
            }
            if (this.selectedContinents.includes('Aasia')) {
            console.log('Valitud: Aasia');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getAasiaAndmed);
            }
            if (this.selectedContinents.includes('Okeaania')) {
            console.log('Valitud: Okeaania');
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getOkeaaniaAndmed);
            }
            // Kui pole valitud htegi kontinenti vi on valitud 'Kik riigid'
            if (ajutisedAndmed.length === 0 || this.selectedContinents.includes('Kik riigid')) {
            console.log('Valitud: Default Kik riigid');
            ajutisedAndmed = [];
            ajutisedAndmed = ajutisedAndmed.concat(this.$store.getters.getAndmed);
            }

            //Segan andmed
            for (let i = ajutisedAndmed.length - 1; i > 0; i--) {
                let j = Math.floor(Math.random() * (i + 1));
                [ajutisedAndmed[i], ajutisedAndmed[j]] = [ajutisedAndmed[j], ajutisedAndmed[i]];
            }
            console.log(ajutisedAndmed.length)
            //Vtan questionCount jagu andmeid
            if(ajutisedAndmed.length < this.questionCount){
                ajutisedAndmed = ajutisedAndmed.slice(0, ajutisedAndmed.length);
            }else{
                ajutisedAndmed = ajutisedAndmed.slice(0, this.questionCount);
            }
            //Panen muudetud andmed Store'isse
            this.$store.commit('impordiAndmed', {
                    muudetudAndmed: ajutisedAndmed
            });
        }
},
}
</script>