import { createRouter, createWebHistory } from 'vue-router'
import mnguMen from "../views/testmng.vue";
import mnguLpp from "../views/mnguLpp.vue";
import Avaleht from "../views/Avaleht.vue";
import Pealinnad from "../views/Pealinnad.vue";
import Lipud from "../views/Lipud.vue";
import Tehnikad from "../views/Tehnikad.vue";
import Meie from "../views/Meie.vue";
import LippudeMng from "../views/LippudeMng.vue";
import PealinnadeMng from "../views/PealinnadeMng.vue";
import LippudeMngRiikideNimedega from "../views/LippudeMng2.vue";
import Postitus from "../views/postitus.vue";


const routes = [{
        path: '/',
        name: 'mnguMen',
        component: mnguMen,
    },
    {
        path: "/pealinnad",
        name: "Pealinnad",
        component: Pealinnad,
    },
    {
        path: "/lipud",
        name: "Lipud",
        component: Lipud,
    },
    {
        path: "/tehnikad",
        name: "Tehnikad",
        component: Tehnikad,
    },
    {
        path: "/meie",
        name: "Meie",
        component: Meie,
    },
    {
        path: "/lippude-mng",
        name: "LippudeMng",
        component: LippudeMng,
    },
    {
        path: "/mnguMen",
        name: "mnguMen",
        component: mnguMen,
    },
    {
        path: "/pealinnade-mng",
        name: "PealinnadeMng",
        component: PealinnadeMng,
    },
    {
        path: "/lippude-mng2",
        name: "LippudeMngRiikideNimedega",
        component: LippudeMngRiikideNimedega,
    },
    {
        path: "/mnguLpp",
        name: "mnguLpp",
        component: mnguLpp,
    },
    {
        path: "/postitus/:id",
        name: "postitus",
        component: () => import("../views/postitus.vue"),
    },
    { //will route to Avaleht view if none of the previous routes apply
        path: "/:catchAll(.*)",
        name: "mnguMen",
        component: mnguMen,
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})



export default router