<template>
    <div class="containerOuter">
        <div class="containerInner">
            <div v-if="!tpsemadTulemused">
                <h1>Mng on lppenud</h1>
                <h2>Sinu tulemus: {{igestiVastatudProtsent}}%</h2>

                <div v-if="igestiVastatudProtsent >= 50 && igestiVastatudProtsent <= 100">
                    <img class="tulemusePildid" :src="require('@/assets/tulemusHea.png')" alt="First Image">
                    <h2>Pris osav oled :)</h2>
                </div>
                <div v-else>
                    <img class="tulemusePildid" :src="require('@/assets/tulemusHalb.png')" alt="Second Image">
                    <h2>Harjuta veel :)</h2>
                </div>
                
                <br>
                <br>
                <div class="alumisedNupud">
                    <button @click="vaataTulemusi"> Vaata tulemusi</button>
                    <button @click="mineMnguMensse">Mngi uuesti!</button>
                </div>
            </div>
            <div v-if="tpsemadTulemused">
                <h1>Sinu tulemused</h1>
                <div class="tpsemadTulemused">
                    <div>
                        <h3>Valitud mng:</h3>
                        <p>{{ selectedGame }}</p>
                    </div>
                    <div>
                        <h3>Valitud kontinendid:</h3>
                        <p>{{ selectedContinents.join(', ') }}</p>
                    </div>
                    <div>
                        <h3>Ksimuste arv:</h3>
                        <p>{{ questionCount }}</p>
                    </div>
                    <div>
                        <h3>Skoor:</h3>
                        <p>{{ skoor }}</p>
                    </div>
                    <div>
                        <h3>igesti vastatud protsent:</h3>
                        <p>{{ igestiVastatudProtsent }}%</p>
                    </div>
                    <div>
                        <h3>Kas mngisid vihjetega?</h3>
                        <p>{{ vihjetegaMng ? 'Jah' : 'Ei' }}</p>
                    </div>
                    <div>
                        <h3>Kasutatud vihjete arv:</h3>
                        <p>{{ kasutatudVihjeteArv }} - Hetkel veel ei tta</p>
                    </div>
                    <br>
                </div>
                <div class="alumisedNupud">
                    <button @click="vaataTulemusi"> Tagasi</button>
                    <button @click="mineMnguMensse">Mngi uuesti!</button>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
import { mapGetters } from 'vuex';

export default {
    name: "getMnguLpp",
        data() {
            return {
                mngValitud: false,
                tpsemadTulemused: false,
                /////////////
                selectedGame: null,
                questionCount: 0,
                selectedContinents: [],
                vihjetegaMng: null,
                skoor: 0,
                kasutatudVihjeteArv: 0,
                igestiVastatudProtsent: 0,


            };
        },
    computed: {
        ...mapGetters(['getTestInfo']),
    },
    methods: {
        mineMnguMensse() {
            this.$router.push("/mnguMen");
        },
        vaataTulemusi(){
            if(!this.tpsemadTulemused){
                this.tpsemadTulemused = true;
            }else{
                this.tpsemadTulemused = false;
            }
        }
    },
    async created() {
        console.log('Created, judsin mnguLpp.vue-sse');
    
        
        // Vtan lppenud mngu tulemused
        const { selectedGame, questionCount, selectedContinents, vihjetegaMng, testResults } = this.getTestInfo;

        this.selectedGame = selectedGame;
        if(selectedGame === 'pealinnadeMng'){
            this.selectedGame = 'Pealinnade mng';
        }else if(selectedGame === 'lippudeMng'){
            this.selectedGame = 'Lippude mng';
        }else if(selectedGame === 'lippudeMng2'){
            this.selectedGame = 'Lippude mng 2';
        }
        this.questionCount = questionCount;
        this.selectedContinents = selectedContinents;
        this.vihjetegaMng = vihjetegaMng;
        this.skoor = testResults.skoor;
        this.kasutatudVihjeteArv = testResults.kasutatudVihjeteArv;
        this.igestiVastatudProtsent = (testResults.skoor / questionCount) * 100;

        console.log('Selected game:', selectedGame);
        console.log('Question count:', questionCount);
        console.log('Selected continents:', selectedContinents);
        console.log('Vihjetega mng:', vihjetegaMng);
        console.log('Test results:', testResults);
        console.log('Skoor:', testResults.skoor);
        console.log('Kasutatud vihjete arv:', testResults.kasutatudVihjeteArv);
    
    }, // created
    
};
</script>

<style scoped>
.containerOuter{
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    padding-top: 5ch;
    color: #55E0E5;
    margin-bottom: 100px;
    user-select: none;
   }
.containerInner{
    background-color: #0B1C24;
    color: #55E0E5;
    border-radius: 36px;
    padding-top: 20px;
    padding-bottom: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding-left: 10ch;
    padding-right: 10ch;    
  }
  .alumisedNupud {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }
  .alumisedNupud button {
    margin: 0 10px;
  }
  button {
    background-color: #55E0E5;
    margin-top: 30px;
    border-radius: 36px;
    border: 0;
    font-weight: 700;
    font-size: 1em;
    padding: 10px 16px;
    letter-spacing: 2px;
    cursor: pointer;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #33d9ff;
}
.tulemusePildid{
    width: 250px;
    height: auto;
}

.tpsemadTulemused>div{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.tpsemadTulemused>div>h3{
    margin-right: 10px;
}
</style>
