<template>
    <div v-if="currentQuestion && kasAndmedOnLaetud" class="Test-Lipud">
        <div class="questionBoxiPeal">
            <h2>TEST: LIPUD 2</h2>
            <div class="question-counter">
                {{ praeguseKsimuseIndeks + 1 }} / {{ andmed.length }}
            </div>
        </div>
        <div class="test-progress">
             <progress class="progress-bar" :value="praeguseKsimuseIndeks" :max="andmed.length-1"></progress>
        </div>
        <div class="KeskmineContainer">
          <div class="QuestionContainer" :style="{ 'padding-right': vihjetegaMng ? '20px' : '10ch' }">
                <div>
                    <div class="ksimusJaVastusevariandid">
                            <!--
                              <h2 class="ksimus" v-if="currentQuestion">{{currentQuestion.question }}</h2>
                            -->
                            <img class="lipupilt" :src="require(`@/assets/lipud/${currentQuestion.lipp}`)" :alt="currentQuestion.lipp">
                    </div>
                    <div class="valikuVariandid">
                        <ul class="answer-grid">
                            <li v-for="(choice, index) in currentQuestion.choices" :key="index" class="answer-option">
                              <!--
                                <button class="radio-button" @click="valitudVastus = lipp"
                                    :class="{'green': kontrollitud && lipp === this.currentQuestion.lipp,
                                             'red': kontrollitud && lipp !== this.currentQuestion.lipp && lipp === valitudVastus}"
                                    :disabled="kontrollitud">
                                    <img :src="require(`@/assets/lipud/${lipp}`)" :alt="lipp">
                                  </button>
                              -->
                              <button class="radio-button" @click="valitudVastus = choice"
                              :class="{'green': kontrollitud && choice === this.currentQuestion.question,
                                       'red': kontrollitud && choice !== this.currentQuestion.question && valitudVastus === choice}" 
                                    :disabled="kontrollitud">{{ choice }}</button>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="vihjeDiv" v-if="vihjetegaMng">
                    <button class="nitaVihjetNupp" @click="nitaVihjet">Vihje</button>
                    <div v-if="vihje || kasOnValeVastus" class="vihjeContainer">
                      <p v-html="currentQuestion.seosLipp"></p>
                        <img v-if="!kasNitanVihjePilti" class="vihjePilt" :src="require(`@/assets/avaVihje.png`)" @click="nitaVihjePilti" alt="Ava vihje pilt">
                        <img v-if="kasNitanVihjePilti" class="vihjePilt" :src="require(`@/assets/lipuseosed/${currentQuestion.seosLippPilt}`)" alt="Seose pilt">
                    </div>
                </div>
            </div>  <!-- QuestionContainer -->
            <div class="kontrolliJajrgmineNupud">
                <button v-if="!kontrollitud" @click="kontrolliVastust">Kontrolli</button>
                <button v-if="kontrollitud && kasOnOlemasJrgmineKsimus" @click="jrgmineKsimus">Jrgmine ksimus</button>
                <button v-if="kontrollitud && !kasOnOlemasJrgmineKsimus" @click="lpetaTest">Lpeta test</button>
            </div>
        </div>
    </div>
   </template>
  
   <script>
export default {
    data() {
       return {
         andmed: [],
         kasAndmedOnLaetud: false,
         kasTestOnLpetatud: false,
         vihje: false,
         kontrollitud: false,
         praeguseKsimuseIndeks: 0,
         valitudVastus: "",
         skoor: 0,
         kasutatudVihjeteArv: 0,
         igestiVastatud: [],
         allChoices: [],
         seosLipp: 'ei ole seost',
         seosLippPilt: '',
         kasNitanVihjePilti: false,
         vihjetegaMng: null,
       };
    },
    computed: {
        currentQuestion() {
            if (this.andmed != null && this.andmed.length > 0) {
                return this.andmed[this.praeguseKsimuseIndeks] || null;
            }
        },
        kasOnOlemasJrgmineKsimus() {
            if (this.andmed != null)
                return this.praeguseKsimuseIndeks < this.andmed.length - 1;
        },
        kasOnigeVastus(){
            return this.valitudVastus === this.currentQuestion.lipp;
        },
        kasOnValeVastus(){
          return this.valitudVastus !== null && this.valitudVastus !== this.currentQuestion.question && this.kontrollitud
        }
    },
    async created() {
        console.log('Created, Judsin lippudeMngu');
    
    try {

        //Kasutatud vihjete arvu nullimine
        this.kasutatudVihjeteArv = 0;

        // Teen selgeks, kas mng tuleb vihjetega vi ilma
        this.vihjetegaMng = this.$store.getters.getKasKasutanVihjeid;

        // ANDMED TULEVAD JSON FAILIST lbi store'i
        let kikAndmed = require('/public/riigid.json');
        
        let imporditudAndmed = this.$store.getters.getMuudetudAndmed;
        console.log('Imporditud andmed:', imporditudAndmed);

        //Unwrappin proxy objecti, et pseda ligi arrayle
        imporditudAndmed = JSON.parse(JSON.stringify(imporditudAndmed));
        
        let ajutineData = imporditudAndmed.muudetudAndmed;

        this.andmed = ajutineData.map(item => {
            if(!item.nimi || !item.pealinn || !item.lipp || !item.seosLipp || !item.seosLippPilt) {
                console.log('Andmed on puudulikud');
            }
            return{
                question: item.nimi,
                pealinn: item.pealinn,
                lipp: item.lipp,
                seosLipp: item.seosLipp,
                seosLippPilt: item.seosLippPilt,
            }

        });
        console.log('Andmed:', this.andmed);


        // Vastusevariantideks on kikide teiste riikide lipud.
        // Nimed pead vtma otse json failist, mitte lalpool mapitud andmetest
        for (let i = this.andmed.length - 1; i > 0; i--) {
            kikAndmed.forEach(item => {
                this.allChoices.push(item.nimi);
            });
        }

        // Mrgin ra iga "ksimuse" ige vastuse
        this.andmed.map(question => question.question);

        // Panen paika iga ksimuse vastusevariandid (mis sisaldab iget vastust ja kolme suvalist vale vastust)
        this.andmed.forEach(question => {
            question.choices = this.vtaSuvalisedVastused(question.question);
        });

    } catch (error) {
        console.log("Tekkis error")
        console.error(error);
    } finally {
        this.kasAndmedOnLaetud = true;
    }

},
    methods: {

        /* Kui vajutatakse vihje pilti */
        nitaVihjePilti() {
            this.kasNitanVihjePilti = true;
            console.log('Vihje pilti vajutati')
;        },

        /* Kui vajutatakse "Vihje" nuppu */
        nitaVihjet() {
            console.log('Vihje nuppu vajutati');
            if(this.vihje){
                this.vihje = false;
            }else{
              if (this.isLoading) return;
              this.vihje = true;
            }
        },
        /* Kui vajutatakse "Kontrolli" nuppu */
       kontrolliVastust() {
            if (this.isLoading) return;

             // Kontrollin, kas kasutaja on vastuse valinud
             if (this.valitudVastus === "") {
                alert("Palun vali vastus!");
                return;
            }
            //alert(this.valitudVastus === this.currentQuestion.correctAnswer ? "ige vastus!" : "Vale vastus!");
            
            this.kontrollitud = true;

            /* Kui on ige vastus ja ei ole veel selle ksimuse eest punkti saanud, siis suurendame skoori */
            if (this.valitudVastus === this.andmed[this.praeguseKsimuseIndeks].question && !this.igestiVastatud[this.praeguseKsimuseIndeks]) {
                this.skoor++;
                this.igestiVastatud[this.praeguseKsimuseIndeks] = true;
            }
       },

        /* Kui vajutatakse "Jrgmine ksimus" nuppu */
       jrgmineKsimus() {
        if (this.isLoading) return;
         this.praeguseKsimuseIndeks++;
         this.vihje = false;
         this.kasNitanVihjePilti = false;
         this.valitudVastus = "";
         this.kontrollitud = false;
       },

       /* Kui vajutatakse "Lpeta test" nuppu */
       lpetaTest() {
         this.kasTestOnLpetatud = true;
         console.log("Test on lpetatud, skoor on: " + this.skoor);
         this.$emit('test-lpetatud', true);

         //Salvestan mngu andmed, et neid nidata lpu ekraanil
         this.$store.commit('saveTestResults', {
         skoor: this.skoor,
         kasutatudVihjeteArv: this.kasutatudVihjeteArv,
        });
       },

       /* Vtan vastusevariantideks suvalised vastused */
       vtaSuvalisedVastused(lipp) {
            if (this.isLoading) return;
            let choices = [...this.allChoices];
            choices = choices.filter(choice => choice !== lipp);
            for (let i = choices.length - 1; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                [choices[i], choices[j]] = [choices[j], choices[i]];
            }
            choices = choices.slice(0, 3).concat(lipp);
            for (let i = choices.length - 1; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                [choices[i], choices[j]] = [choices[j], choices[i]];
            }
            return choices;
        },
    },
   };
   </script>

   <style scoped>
   .Test-Lipud{
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    padding-top: 5ch;
    color: #55E0E5;
    margin-bottom: 100px;
    user-select: none;
   }
   .QuestionContainer {
     display: grid;
    grid-template-columns: 1fr auto;
    background-color: #0B1C24;
    color: #55E0E5;
    border-radius: 36px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding-left: 10ch;
  }
  .keskmineContainer {
    display: inline-flex;
}
  .ksimus {
    font-size: 1.5em;
    margin-bottom: 10px;
  }
  h2.ksimus {
    background-color: #55E0E5;
    color: black;
    border-radius: 15px; 
    text-transform: uppercase;
    padding: 8px;
    user-select: none;
  }
  .nitaVihjetNupp {
    background-color: #55E0E5;
    border-radius: 10px;
    border: 0;
    font-weight: 700;
    font-size: 0.8em;
    display: block;
    padding: 10px 16px;
    letter-spacing: 1px;
    align-items: right;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  .nitaVihjetNupp:hover {
    background-color: #33d9ff;
  }
  .vihjeContainer{
    border-radius: 20px;
    background-color: black;
    border: 2px solid #55E0E5;
    padding: 20px;
    max-width: 300px;
}
  .vihjePilt{
    width: 200px;
    height: auto;
    border-radius: 15px;
  }
  .ksimusJaVastusevariandid {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: 2ch;
    border-radius: 15px;
    background-color: #0B1C24;
    color: #55E0E5;
    font-size: 16px;
  }
  .lipupilt > img{
    width: 150px;
    height: 75;
    border-radius: 25px; 
  }
  .vihjeDiv{
    margin-left: 20px;
  }
   .questionBoxiPeal{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-bottom: 0;
   }
   
   .question-counter{
    padding-right: 1ch;
    font-size: 25px;
   }
   .kontrolliJajrgmineNupud{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
   }
   .kontrolliJajrgmineNupud > button {
    background-color: #55E0E5;
    margin-right: 20px;
    margin-top: 30px;
    border-radius: 36px;
    border: 0;
    font-weight: 700;
    font-size: 0.8em;
    display: block;
    padding: 10px 16px;
    letter-spacing: 2px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  .kontrolliJajrgmineNupud > button:hover {
    background-color: #33d9ff;
  }

   .answer-grid {
    display: grid;
    grid-template-columns: repeat(2, 0fr);
    row-gap: 1ch; /* vertical gap */
    column-gap: 4ch; /* horizontal gap */
    list-style-type: none;

   }
   ul.answer-grid {
    padding-left: 0;
  }
  
   .answer-option img{
    display: flex;
    align-items: center;
    text-align: center;
    width: 300;
    height: 150;
    border-radius: 15px;
   }


   /* Vastusevalikute nupud*/
   .radio-button {
    display: block;
    width: 100%;
    padding: 10px;
    border: 2px solid #55E0E5;
    border-radius: 15px;
    background-color: #0B1C24;
    color: #55E0E5;
    font-size: 16px;
    cursor: pointer;
  }
  .green{
    background-color: green;
    border: 2px solid black;
  }
  .red{
    background-color: red;
    border: 2px solid black;
  }

  .radio-button:hover {
    background-color: #55E0E5;
    color: black;
  }
  .radio-button:focus {
    background-color: #55E0E5;
    color: black;
  }
  .radio-button:checked {
    background-color: #55E0E5;
    color: black;
  }
  li::marker{
    visibility: hidden;
    position: absolute;
  }
  .test-progress{
    width: 100%;
    height: 10px;
    border-radius: 5px;
    overflow: hidden; 
  }
  progress[value] {
    --w: 100%; /* the width*/
    --color:  /* the progress color */
      linear-gradient(90deg,#2d414b,#55e0e5) 0/var(--w);
    --background: lightgrey; /* the background color */

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    width: var(--w);
    margin: 0 10px;
    border-radius: 10em;
    background: var(--background);
  }
  progress[value]::-webkit-progress-bar {
    border-radius: 10em;
    background: var(--background);
  }
  progress[value]::-webkit-progress-value {
    border-radius: 10em;
    background: var(--color);
  }
  progress[value]::-moz-progress-bar {
    border-radius: 10em;
    background: var(--color);
  }
   </style>